@import url("https://fonts.googleapis.com/css2?family=DM+Serif+Display&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Karla:wght@400;700&display=swap");
:root {
  /*-- Primary --*/
  --darkViolet: hsl(256, 26%, 20%);
  --grayishBlue: hsl(0, 0%, 63%);
  /*---- Neutral ----*/
  --vd-Violet: hsl(270, 9%, 17%);
  --dg-Violet: hsl(274, 16%, 9%);
  --white: hsl(0, 0%, 98%);
}

:root {
  --font-normal: 400;
  --font-bold: 700;
  --font-Karla: "Karla", sans-serif;
  --font-DM-Serif: "DM Serif Display", serif;
}

.footer__social, .btn {
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer__icon, .cta__container, .header .nav__button {
  display: flex;
  justify-content: space-between;
}

.hero__container, .hero__content, .features__container, .header .nav__list {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

@keyframes animationX {
  0% {
    opacity: 0;
    transform: translateX(-150px);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
@keyframes animationY {
  0% {
    opacity: 0;
    transform: translateY(-150px);
  }
  100% {
    opacity: 1;
    transform: translate(0);
  }
}
@keyframes shake {
  0% {
    opacity: 0;
    transform: translateY(4.125rem) scale(0.8) rotate(5deg);
  }
  50% {
    transform: translateY(4.125rem) scale(0.8) rotate(-5deg);
  }
  100% {
    opacity: 1;
    transform: translateY(4.125rem) scale(1) rotate(0deg);
  }
}
html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-size: 1rem;
  font-weight: var(--font-normal);
  font-family: var(--font-Karla);
}
body.no-scroll {
  overflow: hidden;
}
@media (min-width: 50em) {
  body.no-scroll {
    overflow-y: scroll;
  }
}

ul,
ol {
  list-style: none;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
}

a,
a:visited,
a:active {
  text-decoration: none;
}
a:focus,
a:visited:focus,
a:active:focus {
  outline: none;
}
a:focus-visible,
a:visited:focus-visible,
a:active:focus-visible {
  outline: 0.1875rem dashed var(--darkViolet);
}

h1,
h2,
h3 {
  margin: 0;
  font-family: var(--font-DM-Serif);
}

p {
  margin: 0;
}

.heading, .heading--cta, .heading--l, .heading--xl, .heading--xxl {
  font-weight: var(--font-normal);
}
.heading--xxl {
  position: relative;
  font-size: clamp(2.875rem, 9vw, 4.375rem);
  color: var(--white);
  line-height: 4rem;
  margin-bottom: 1.875rem;
  max-width: 31.25rem;
}
@media (min-width: 50em) {
  .heading--xxl::before {
    content: "";
    position: absolute;
    top: -4rem;
    left: 0;
    height: 0.0625rem;
    width: 9.4375rem;
    background-color: var(--grayishBlue);
  }
}
.heading--xl {
  font-size: clamp(3rem, 7vw, 4.4375rem);
}
.heading--l {
  font-size: 1.75rem;
}
.heading--cta {
  font-size: clamp(1.875rem, 5vw, 3rem);
  color: var(--white);
}

.paragraph, .paragraph--l, .paragraph--xl {
  font-weight: var(--font-normal);
  color: var(--grayishBlue);
  line-height: 1.625rem;
}
.paragraph--xl {
  font-size: 1rem;
  margin-bottom: 1.9375rem;
}
.paragraph--l {
  font-size: 1.0625rem;
}

.container {
  max-width: 71.25rem;
  width: 100%;
  margin: 0 auto;
  padding: 0 1rem;
}

.divider {
  width: 9.4375rem;
  height: 0.1875rem;
  background-color: var(--grayishBlue);
  margin-bottom: 1.875rem;
}
@media (min-width: 50em) {
  .divider {
    margin-left: 0;
  }
}

.btn {
  font-size: 0.9375rem;
  font-weight: var(--font-bold);
  outline: none;
  border: none;
  cursor: pointer;
  transition: background-color 350ms ease-in, border 300ms ease-in;
}
.btn:focus {
  outline: none;
}
.btn:focus-visible {
  outline: 0.1875rem dashed var(--darkViolet);
  outline-offset: 0.25rem;
}
.btn--menu {
  background-color: transparent;
  transition: 0.5s;
}
@media (min-width: 50em) {
  .btn--menu {
    display: none;
  }
}
.btn--plans {
  width: 86vw;
  border: 0.125rem solid var(--white);
  color: var(--white);
  text-align: center;
  text-transform: uppercase;
  font-size: 1.375rem;
  height: 3.375rem;
}
@media (min-width: 50em) {
  .btn--plans {
    background-color: transparent;
    width: 9rem;
    height: 2.6875rem;
    color: var(--darkViolet);
    font-size: 1rem;
    border: 0.125rem solid var(--darkViolet);
  }
  .btn--plans:hover {
    background-color: var(--darkViolet);
    color: var(--white);
  }
  .btn--plans:active {
    box-shadow: inset 0 -0.25rem 0 rgba(0, 0, 0, 0.1), 0 0 1px 0.15rem #fff, 0 0 1px 0.3rem var(--darkViolet);
  }
}
.btn--white, .btn--cta,
.btn #btn--plans {
  background-color: transparent;
  border: 0.125rem solid var(--white);
  color: var(--white);
  padding: 0.5625rem 2.1875rem;
  z-index: 10;
}
.btn--white:hover, .btn--cta:hover,
.btn #btn--plans:hover {
  background-color: var(--white);
  color: var(--darkViolet);
}
.btn--white:focus, .btn--cta:focus,
.btn #btn--plans:focus {
  outline: none;
}
.btn--white:focus-visible, .btn--cta:focus-visible,
.btn #btn--plans:focus-visible {
  outline: 3px dashed var(--white);
  outline-offset: 3px;
}

.header {
  position: relative;
  z-index: 3;
  background-color: var(--white);
}
.header .nav {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
}
@media (min-width: 50em) {
  .header .nav {
    flex-direction: row;
    justify-content: space-between;
    padding: 1.25rem 0;
  }
}
.header .nav__button {
  align-items: center;
  width: 100%;
  padding: 1.375rem 0;
}
@media (min-width: 50em) {
  .header .nav__button {
    padding: 0;
  }
}
.header .nav__menu {
  position: absolute;
  top: 5rem;
  left: 0;
  width: 100%;
  height: 0;
  overflow: hidden;
  z-index: 100;
  transition: all 0.6s ease;
}
@media (min-width: 50em) {
  .header .nav__menu {
    position: unset;
    flex: 1 0 80%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    transition: none;
    height: auto;
  }
}
.header .nav__menu.show {
  background-color: var(--vd-Violet);
  height: calc(100vh - 5rem);
}
@media (min-width: 50em) {
  .header .nav__menu.show {
    height: auto;
    background-color: var(--white);
  }
}
.header .nav__menu::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  height: 14.4375rem;
  width: 100%;
  background-image: url("/images/bg-pattern-mobile-nav.svg");
  background-position: bottom;
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
@media (min-width: 50em) {
  .header .nav__menu::after {
    display: none;
  }
}
.header .nav__list {
  margin: 0;
  padding: 3.0625rem 0 0;
  gap: 2.25rem;
}
@media (min-width: 50em) {
  .header .nav__list {
    display: flex;
    flex-direction: row;
    gap: 1.875rem;
    padding: 0;
  }
}
.header .nav__link {
  position: relative;
  font-size: 1.375rem;
  color: var(--white);
  text-transform: uppercase;
}
.header .nav__link::after {
  content: "";
  position: absolute;
  bottom: -0.3125rem;
  left: 0;
  width: 100%;
  height: 0.125rem;
  background-color: var(--darkViolet);
  transform: scale(0);
  visibility: hidden;
  opacity: 0;
  transition: all 380ms ease-in;
}
@media (min-width: 50em) {
  .header .nav__link {
    color: var(--grayishBlue);
    font-size: 1rem;
  }
  .header .nav__link:hover {
    color: var(--darkViolet);
  }
  .header .nav__link:hover::after {
    transform: scale(1);
    visibility: visible;
    opacity: 1;
  }
}

.hero {
  background-color: var(--darkViolet);
}
.hero::before {
  content: url("/images/bg-pattern-intro-left-mobile.svg");
  position: absolute;
  top: 33rem;
  left: 0;
}
@media (min-width: 50em) {
  .hero::before {
    content: url("/images/bg-pattern-intro-left-desktop.svg");
    position: absolute;
    top: 31.6875rem;
    left: 0;
    z-index: 1;
  }
}
.hero::after {
  content: url("/images/bg-pattern-intro-right-mobile.svg");
  position: absolute;
  bottom: -34.8125rem;
  right: 0;
  z-index: 1;
}
@media (min-width: 50em) {
  .hero::after {
    content: url("/images/bg-pattern-intro-right-desktop.svg");
    position: absolute;
    right: 0;
    top: 0;
    z-index: 1;
  }
}
.hero__container {
  max-width: 71.875rem;
  width: 100%;
  margin: 0 auto;
  position: relative;
}
@media (min-width: 50em) {
  .hero__container {
    flex-direction: row;
    padding: 0 1.4375rem;
  }
}
.hero__img {
  width: 100%;
  z-index: 1;
}
@media (min-width: 50em) {
  .hero__img {
    order: 2;
    flex: 1 0 49%;
  }
}
.hero__img--images {
  width: 100%;
  object-fit: cover;
  height: 28.375rem;
  z-index: 1;
  animation: animationY 1.8s ease;
}
@media (min-width: 50em) {
  .hero__img--images {
    height: 31.3125rem;
    transform: translateY(3.125rem);
    animation: shake 1s ease;
  }
}
@media (min-width: 64em) {
  .hero__img--images {
    height: 37.5rem;
    transform: translateY(6.25rem);
    animation: shake 1s ease;
  }
}
.hero__content {
  position: relative;
  padding: 5.125rem 1.0625rem;
  text-align: center;
  animation: animationY 1.4s ease;
}
@media (min-width: 50em) {
  .hero__content {
    animation: animationX 1.4s ease;
    text-align: left;
    padding: 0;
    padding-right: 1.4375rem;
    align-items: flex-start;
    margin-top: 2.6875rem;
  }
}

/*--- Features Section -----*/
.features {
  position: relative;
  background-color: var(--white);
  padding: 18.25rem 0 10.3125rem;
}
@media (max-width: 49.9375em) {
  .features {
    padding: 8.25rem 0 7.75rem;
  }
}
.features__container {
  text-align: center;
  max-width: 76.125rem;
  padding: 0 3rem;
}
@media (min-width: 50em) {
  .features__container {
    text-align: left;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
  }
}
.features__feature {
  margin-top: 4.375rem;
  display: grid;
  justify-items: center;
  align-items: center;
  grid-template-columns: 1fr;
  grid-gap: 2.9375rem;
}
@media (min-width: 37.5em) {
  .features__feature {
    grid-template-columns: 1fr 1fr;
    justify-items: flex-start;
    align-items: flex-start;
  }
}
@media (min-width: 50em) {
  .features__feature {
    grid-template-columns: repeat(3, 1fr);
  }
}
.features__feature--content {
  display: grid;
  justify-items: center;
  align-items: center;
  grid-gap: 1.875rem;
}
@media (min-width: 50em) {
  .features__feature--content {
    justify-items: flex-start;
    align-items: flex-start;
  }
}

/*-- cta --*/
.cta {
  position: relative;
  padding: 0 1.25rem;
}
.cta__container {
  align-items: center;
  flex-direction: column;
  text-align: center;
  background-image: url("/images/bg-pattern-how-we-work-mobile.svg");
  background-position: top right;
  background-repeat: no-repeat;
  background-size: auto;
  background-color: var(--darkViolet);
  padding: 5rem 1.25rem;
  height: 22.5rem;
  width: 100%;
}
@media (min-width: 50em) {
  .cta__container {
    background-image: url("/images/bg-pattern-how-we-work-desktop.svg");
    background-position: top right;
    background-repeat: no-repeat;
    background-size: auto;
  }
}
@media (min-width: 50em) {
  .cta__container {
    flex-direction: row;
    padding: 1.25rem 7.25rem;
    height: 16.25rem;
    text-align: left;
  }
}

.footer {
  position: relative;
  margin: 9.875rem 0 0;
  padding-bottom: 1.75rem;
  background-color: #fafafa;
  background-image: url("/images/bg-pattern-footer-mobile.svg");
  background-position: top left;
  background-repeat: no-repeat;
  background-size: 100%;
}
@media (min-width: 50em) {
  .footer {
    background-image: url("/images/bg-pattern-footer-desktop.svg");
    background-position: top left;
    background-repeat: no-repeat;
    background-size: auto;
  }
}
.footer__container {
  display: grid;
  justify-items: center;
  align-items: center;
  position: relative;
}
@media (max-width: 49.9375em) {
  .footer__container {
    text-align: center;
  }
}
.footer__icon {
  flex-direction: column;
  align-items: center;
  gap: 1.875rem;
  width: 100%;
  padding: 5.75rem 0 2.25rem;
  border-bottom: 0.125rem solid rgba(50, 50, 50, 0.2);
}
@media (min-width: 50em) {
  .footer__icon {
    flex-direction: row;
    padding: 3.5rem 0 2.375rem;
  }
}
.footer__social {
  gap: 0.9375rem;
}
.footer__social .social-icon:hover * {
  fill: var(--darkViolet);
  transition: all 0.3s;
}
.footer__nav {
  display: grid;
  justify-content: center;
  grid-template-columns: max-content;
  grid-gap: 1.375rem;
  padding-top: 2.3125rem;
}
@media (min-width: 50em) {
  .footer__nav {
    grid-template-columns: repeat(2, 1fr);
    width: 100%;
  }
}
@media (min-width: 64em) {
  .footer__nav {
    grid-template-columns: repeat(4, 1fr);
  }
}
.footer__nav .nav {
  display: grid;
  grid-gap: 1.25rem;
}
.footer__nav .nav__title {
  color: var(--grayishBlue);
  font-size: 0.9375rem;
  margin: 0;
}
.footer__nav .nav__list {
  display: grid;
  grid-auto-flow: row;
  grid-gap: 0.6875rem;
}
.footer__nav .nav__link {
  color: var(--black);
  font-size: 1rem;
  position: relative;
}
.footer__nav .nav__link:hover {
  text-decoration: underline;
}